import React from 'react';

const AnnouncementTemplate = ({ data }) => {
    const { title, subtitle, date, image, message, variant = 0, location, time, phone, backgroundType, backgroundImage } = data;

    const backgroundStyle = backgroundType === 'image' && backgroundImage ? {
        backgroundImage: `url(${backgroundImage})`,
        backgroundSize: 'cover',
        backgroundPosition: 'center'
    } : {};

    const ImenaBranding = () => (
        <div className="absolute bottom-4 right-4 z-[60] flex items-center gap-2 opacity-30 select-none pointer-events-none grayscale">
            <img src="/imena-logo.svg" alt="Imena" className="h-6 w-auto" />
            <span className="text-[8px] font-bold text-neutral-500 uppercase tracking-[0.2em]">Designed by Imena</span>
        </div>
    );

    // Variant 0: Premium Image Layout
    if (variant === 0 || (backgroundType === 'image' && (variant === 1 || variant === 0))) {
        return (
            <div className="relative w-full h-full bg-neutral-900 flex flex-col items-center justify-center p-12 overflow-hidden font-sans" style={backgroundStyle}>
                <ImenaBranding />
                <div className="absolute inset-0 bg-black/50 backdrop-blur-[2px] z-0"></div>
                <div className="relative z-10 w-full flex flex-col items-center text-center">
                    <div className="bg-gold-500 text-black px-6 py-1 font-black uppercase tracking-[0.3em] text-[10px] mb-8">Official Notice</div>
                    <h1 className="text-5xl font-black uppercase tracking-tighter text-white mb-6 leading-none">{title || "Announcement"}</h1>
                    <div className="w-12 h-1 bg-gold-500 mb-8"></div>
                    <p className="text-white/90 text-sm max-w-sm mb-12 leading-relaxed tracking-wide uppercase font-bold">{message || "Important Information Follows."}</p>
                    <div className="grid grid-cols-2 gap-10 border-t border-white/20 pt-8 w-full max-w-xs">
                        <div className="text-center"><span className="block text-[8px] text-gold-500 mb-1">Date</span>{date || "2024"}</div>
                        <div className="text-center"><span className="block text-[8px] text-gold-500 mb-1">Details</span>{location || "Main Office"}</div>
                    </div>
                </div>
            </div>
        );
    }

    // Variant 1: Official
    if (variant === 1) {
        return (
            <div className="relative w-full h-full bg-white p-12 flex flex-col border-[2px] border-neutral-900" style={backgroundStyle}>
                <ImenaBranding />
                <h1 className="text-4xl font-bold uppercase mb-4">{title}</h1>
                <div className="h-2 w-24 bg-black mb-8"></div>
                <p className="text-xl mb-4 italic">{subtitle}</p>
                <p className="text-neutral-600">{message}</p>
            </div>
        );
    }

    return null;
};

export default AnnouncementTemplate;
