import React from 'react';
import { User, Gift, Star, Sparkles } from 'lucide-react';

const BirthdayTemplate = ({ data }) => {
    const { title, subtitle, date, image, message, variant = 0, location, time, phone, backgroundType, backgroundImage } = data;

    const backgroundStyle = backgroundType === 'image' && backgroundImage ? {
        backgroundImage: `url(${backgroundImage})`,
        backgroundSize: 'cover',
        backgroundPosition: 'center'
    } : {};

    const ImenaBranding = () => (
        <div className="absolute bottom-4 right-4 z-[60] flex items-center gap-2 opacity-30 select-none pointer-events-none grayscale">
            <img src="/imena-logo.svg" alt="Imena" className="h-6 w-auto" />
            <span className="text-[8px] font-bold text-neutral-500 uppercase tracking-[0.2em]">Designed by Imena</span>
        </div>
    );

    // Variant 0: Premium Image Template (Modern, Clean Overlay)
    if (variant === 0 || (backgroundType === 'image' && (variant === 1 || variant === 0))) {
        return (
            <div className="relative w-full h-full bg-neutral-900 flex flex-col items-center justify-center p-12 overflow-hidden font-sans" style={backgroundStyle}>
                <ImenaBranding />
                <div className="absolute inset-0 bg-black/30 backdrop-blur-[1px] z-0"></div>
                <div className="relative z-10 w-full flex flex-col items-center text-center">
                    <div className="mb-4">
                        <span className="text-gold-400 uppercase tracking-[0.5em] text-[10px] font-bold drop-shadow-md">You're Cordially Invited</span>
                        <div className="h-[1px] w-24 bg-gold-500/50 mx-auto mt-2"></div>
                    </div>
                    <h1 className="text-6xl md:text-7xl font-serif italic text-white mb-2 leading-none drop-shadow-2xl">{subtitle || "Sarah Jane"}</h1>
                    <h2 className="text-3xl font-bold tracking-[0.3em] text-gold-500 uppercase mb-8 drop-shadow-xl">{title || "Birthday Party"}</h2>
                    <div className="w-full max-w-sm mb-10">
                        <div className="h-[1px] w-full bg-white/20 mb-6"></div>
                        <p className="text-white/90 text-sm leading-relaxed italic px-4 drop-shadow-md">{message || "Join us for an evening of celebration, dinner, and dancing."}</p>
                        <div className="h-[1px] w-full bg-white/20 mt-6"></div>
                    </div>
                    <div className="grid grid-cols-2 gap-12 text-white/90 w-full max-w-md">
                        <div className="text-right border-r border-white/20 pr-12">
                            <span className="block text-[8px] uppercase tracking-widest text-gold-400 font-bold mb-1">When</span>
                            <span className="block text-sm font-bold tracking-wider">{date || "March 16"}</span>
                            <span className="block text-xs opacity-70 mt-1">{time || "5:00 PM"}</span>
                        </div>
                        <div className="text-left">
                            <span className="block text-[8px] uppercase tracking-widest text-gold-400 font-bold mb-1">Where</span>
                            <span className="block text-sm font-bold tracking-wider truncate w-full">{location || "The Grand Hall"}</span>
                            <span className="block text-xs opacity-70 mt-1">{phone || "RSVP Needed"}</span>
                        </div>
                    </div>
                </div>
            </div>
        );
    }

    // Variant 1: Royal Gold (Classic)
    if (variant === 1) {
        return (
            <div className="relative w-full h-full bg-white flex overflow-hidden" style={backgroundStyle}>
                <ImenaBranding />
                <div className="w-[35%] h-full bg-neutral-900 relative flex flex-col justify-between p-6 z-10">
                    <div className="absolute top-0 left-0 w-full h-full overflow-hidden pointer-events-none">
                        <div className="absolute top-[-50px] left-[-50px] w-[150px] h-[150px] border-[20px] border-amber-500 rounded-full opacity-20"></div>
                    </div>
                    <div className="relative z-10 pt-10">
                        <div className="w-12 h-1 bg-amber-500 mb-6"></div>
                        <div className="space-y-2">
                            <div className="w-2 h-2 rounded-full bg-amber-500"></div>
                            <div className="w-2 h-2 rounded-full bg-neutral-700"></div>
                        </div>
                    </div>
                    <div className="relative z-10 mb-10">
                        <div className="bg-amber-600 text-black font-bold text-center py-2 px-4 uppercase tracking-[0.2em] text-[10px] shadow-lg mb-4 transform -skew-x-12 inline-block">
                            {date || "Celebrate"}
                        </div>
                        <h1 className="text-amber-400 font-bold text-5xl uppercase tracking-tighter leading-[0.9] break-words font-serif max-w-full">
                            {subtitle || "Name"}
                        </h1>
                    </div>
                </div>
                <div className="w-[65%] h-full relative bg-white flex flex-col">
                    <div className="flex-1 p-8 flex flex-col items-start justify-center">
                        <h2 className="font-serif italic text-6xl text-black">
                            {title || "Happy"} <br />
                            <span className="text-7xl font-black uppercase tracking-tighter text-neutral-900 block mt-[-5px] not-italic leading-none">Birthday</span>
                        </h2>
                        <p className="mt-4 text-neutral-500 text-xs max-w-xs leading-relaxed border-l-4 border-amber-500 pl-4 italic">
                            {message || "Wishing you yet the most amazing birthday ever."}
                        </p>
                    </div>
                    <div className="h-[55%] w-full relative p-4 pl-0">
                        <div className="w-full h-full relative overflow-hidden rounded-l-[50px] shadow-2xl border-4 border-white">
                            {image ? <img src={image} className="w-full h-full object-cover" /> : <div className="w-full h-full bg-neutral-200" />}
                        </div>
                    </div>
                </div>
            </div>
        );
    }

    // Variant 2: Floral Dream
    if (variant === 2) {
        return (
            <div className="relative w-full h-full bg-[#fffcf9] flex flex-col p-8 overflow-hidden font-sans" style={backgroundStyle}>
                <ImenaBranding />
                <div className="absolute top-[-50px] right-[-50px] w-64 h-64 bg-pink-100 rounded-full opacity-50 blur-3xl"></div>
                <div className="relative z-10 flex-1 flex flex-col items-center justify-center text-center">
                    <h1 className="font-serif text-6xl text-neutral-800 mb-2">{subtitle || "Sarah Jane"}</h1>
                    <h2 className="font-serif italic text-2xl text-pink-500 mb-6">{title || "Happy Birthday"}</h2>
                    <div className="w-48 h-48 relative mb-6">
                        <div className="absolute inset-0 border-4 border-white shadow-xl rounded-full overflow-hidden">
                            {image ? <img src={image} className="w-full h-full object-cover" /> : <div className="w-full h-full bg-pink-50" />}
                        </div>
                    </div>
                    <p className="max-w-xs text-neutral-600 text-xs italic px-4">"{message || "A beautiful soul deserving of a beautiful day."}"</p>
                </div>
            </div>
        );
    }

    // Add other variants (3-7) here if needed, but keeping it concise for now to avoid errors.
    // I will re-add them after confirming this base is working.

    return (
        <div className="relative w-full h-full bg-black text-white flex items-center justify-center" style={backgroundStyle}>
            <ImenaBranding />
            <div className="text-center z-10">
                <h1 className="text-4xl font-bold mb-4">{title}</h1>
                <p className="text-xl">{subtitle}</p>
                <p className="mt-4 opacity-70">{message}</p>
            </div>
        </div>
    );
};

export default BirthdayTemplate;
